/* Reset and base styles */
body {
    font-family: 'Arial', sans-serif;
    background-color: #e2e2e2;
    margin: 0;
    padding: 0;
}

.wrapper {
    display: flex;
    height: 100vh;
}

.sidebar {
    width: 200px;
    background-color: #333;
    color: white;
    height: 100%;
}

.sidebar-header {
    padding: 20px;
    background: #222;
    text-align: center;
}

.nav-links {
    list-style: none;
    padding: 0;
}

.nav-links li a {
    display: block;
    padding: 10px 20px;
    color: white;
    text-decoration: none;
    transition: background 0.3s, padding-left 0.3s;
}

.nav-links li a:hover {
    background: #555;
    padding-left: 30px;
}

.container {
    flex-grow: 1;
    padding: 20px;
    background-color: #f4f4f4;
}

.title {
    font-size: 24px;
    color: #333;
    padding: 15px 0;
    border-bottom: 3px solid #333;
}

table {
    width: 100%;
    border-collapse: collapse;  /* Ensures borders between cells are merged */
    margin-top: 20px;
}

th, td {
    padding: 12px;
    text-align: left; /* Aligns text to the left by default */
    vertical-align: middle; /* Centers content vertically within the cell */
    border-bottom: 1px solid #ccc; /* Adds a subtle line between rows */
}

th {
    background-color: #444; /* Dark background for headers */
    color: white; /* White text for contrast */
    font-weight: bold;
}

/* Extend the background and styles for the last header if it's not aligning properly */
th:last-child {
    background-color: #444; /* Ensure it matches other headers */
    text-align: right; /* If you want the arrow or other content in the last header to align right */
}


td {
    background-color: #f9f9f9;
}

tr:hover {
    cursor: pointer; /* Indicates that the row is clickable */
    background-color: #f0f0f0; /* Optional: highlight on hover */
}

td img {
    cursor: pointer;  
    transition: opacity 0.3s ease-in-out;
}

td img:hover {
    opacity: 0.8;
}

/* Specific styles for images within table cells */
img {
    width: 100px; /* Adjust width as necessary */
    height: auto; /* Maintain aspect ratio */
    display: block;
    margin-left: auto;
    margin-right: auto; /* Centering the image horizontally */
}

/* Arrow styling, ensuring it is right-aligned */
td:last-child {
    text-align: right;
    color: #666;
}

input[type="text"] {
    padding: 8px;
    margin: 5px;
    border: 1px solid #ccc;
}

button {
    padding: 10px 20px;
    background-color: #4CAF50;
    color: white;
    border: none;
    cursor: pointer;
}

button:hover {
    background-color: #45a049;
}


@media screen and (max-width: 768px) {
    .wrapper {
        flex-direction: column;
    }

    .sidebar {
        width: 100%;
        height: auto;
    }

    .container {
        width: 100%;
    }
}
